import axios from "axios";
import { useEffect, useState } from "react";
// import CustomPagination from "../../components/Pagination/CustomPagination";
import SingleContent from "../../components/SingleContent/SingleContent";
import "./Series.css";

const Series = () => {
  const [page, setPage] = useState(1);
  const [content, setContent] = useState([]);
  // const [numOfPages, setNumOfPages] = useState();
  const [cdn, setCdn] = useState('');
  const [googleId, setGoogldId] = useState('');
  const [bigContent, setBigContent] = useState([]);
  const [bigContentNum, setBigContentNum] = useState(0);
  const [realPage, setRealPage] = useState(0);
  const [moreText, setMoreText] = useState('加载更多...');

  const perPage = 10;

  const flushPage = () => {
    if (page * perPage > bigContentNum) {
      fetchNewItems();
      
    } else {
      setContent(content.concat(bigContent.slice((page-1)*perPage, page*perPage)));
    }
  };

  const moreContentClick = () => {
    console.log('==================new page==========');
    setPage(page+1);
  };

  const fetchNewItems = async () => {
    if (cdn !== '') {
      const pageFormatNum = String(realPage).padStart(3, '0');
      try {  
        const {data} = await axios.get(
          // `${cdn}page-${pageFormatNum}.json`
          `${process.env.REACT_APP_TEST_BASEDIR}page-${pageFormatNum}.json`
        );
        setBigContent(bigContent.concat(data));
        setBigContentNum(bigContentNum + data.length);
        setContent(bigContent.slice((page-1)*perPage, page *perPage));
        setRealPage(realPage + 1);
      } catch(err) {
        setMoreText("没有更多了...");
        console.log(err);
      }
    } else {
      axios.get(
        `${process.env.REACT_APP_CONFIG_URL}`
      ).then( async function(res) {
        setCdn(res.data.cdn);
        setGoogldId(res.data.google_statistics_id);
        try {  
          const {data} = await axios.get(
            // `${cdn}page-${pageFormatNum}.json`
            `${process.env.REACT_APP_TEST_BASEDIR}page-000.json`
          );
          setBigContent(data);
          setBigContentNum(data.length);
          setContent(data.slice(0, perPage));
          setRealPage(realPage + 1);
        } catch(err) {
          setMoreText("没有更多了...");
          console.log(err);
        }
      });
    }
  };

  useEffect(() => {
    flushPage();
    // eslint-disable-next-line
  }, [page]);

  return (
    <div className="mContent">
      <div className="trending">
        {content &&
          content.map((c) => (
            <SingleContent
              key={c.slug}
              id={c.slug}
              poster={c.cover}
              title={c.title}
              date={c.update_at}
              media_type="video"
              vote_average='9.0'
              duration={c.duration}
              url={c.url}
              backurl="/"
            />
          ))}
      </div>
      <div className="moreContentLink">
        <div className="linkText" onClick={moreContentClick}>{moreText}</div>
      </div>
    </div>
  );
};

export default Series;
